.hero {
  width: 100%;
  background: #212121 url("../img/hero-bg.jpg") no-repeat center;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 48px 0;
  position: relative;
  z-index: 1;

  &::before {
    display: block;
    content: "";
    width: 100%;
    height: 100%;
    background: rgba(#212121, 0.4);
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
  }

  &__content {
    max-width: 720px;
    margin-bottom: 104px;
  }
  &__h1 {
    color: #fff;
    font-size: 48px;
    font-weight: 400;
    line-height: 1.2;
    margin-bottom: 24px;
    border-left: 4px solid var(--color-primary);
    padding-left: 16px;
  }

  &__descr {
    max-width: 520px;
    color: #fff;
    font-size: 18px;
  }
  &__podbor {
    position: relative;
  }
}

// анимация смена фона
.anim-bg {
  animation: hero-bg_anim linear infinite alternate forwards;
  // // animation-name: hero-bg_anim;
  animation-duration: 21s;
  animation-delay: 15s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  // animation-timing-function: steps(1, end);
}

@keyframes hero-bg_anim {
  0% {
    background-image: url("../img/hero-bg_1.jpg");
    // animation-delay: 3s;
  }
  25% {
    background-image: url("../img/hero-bg_2.jpg");
    // animation-delay: 3s;
  }
  50% {
    background-image: url("../img/hero-bg_3.jpg");
    // animation-delay: 3s;
  }
  75% {
    background-image: url("../img/hero-bg_4.jpg");
    // animation-delay: 3s;
  }
  100% {
    background-image: url("../img/hero-bg_1.jpg");
    // animation-delay: 3s;
  }
}

@media (max-width: 1024px) {
  .hero__h1 {
    font-size: 36px;
  }
}

@media (max-width: 576px) {
  .hero {
    padding: 32px 0;
  }

  .hero__content {
    margin-bottom: 80px;
  }
  .hero__h1 {
    font-size: 28px;
  }

  .hero__descr {
    display: none;
  }
}

@media (max-width: 480px) {
  .hero__h1 {
    font-size: 24px;
    margin-bottom: 16px;
  }

  .hero__descr {
    font-size: 14px;
    line-height: 20px;
  }
}
